<template>
  <div class="zanr mt-3">
    <h1>{{this.zanrName}}</h1>
    <!-- <HelloWorld msg="Filmovi"/> -->
    <FilmComponent v-bind:film="film"/>
    <!-- ovde idu filmovi sinlgle -->

    <!--<div v-for="n in (filmoviZaPrikaz.length < 10 ? filmoviZaPrikaz.length : 10) " :key="n"> 
        <FilmComponent :msg="n"/>
    </div>-->
    <!-- <div v-for="n in (filmoviZaPrikaz.length < 10 ? filmoviZaPrikaz.length : 10) " :key="n">
        <FilmComponent v-bind:film="filmoviZaPrikaz[n - 1]" />
    </div> -->

<!-- filmovi[n].id -->
    <b-button @click="prev()">Previous</b-button>
    <span>...</span>
    <b-button @click="next()">Next</b-button>

    <!-- <div class="overflow-auto" id="ass">
        <b-pagination-nav :link-gen="linkGen" :number-of-pages=this.current use-router></b-pagination-nav>
    </div> -->



  </div>
</template>
<script>
import FilmComponent from '@/components/FilmComponent.vue';
import { mapActions, mapMutations, mapState } from 'vuex';
    export default{
    name: "Filmovi-view",
    data() {
        return {
            zanrName: String,
            zanrId: Number
        };
    },
    mounted() {
        this.$forceUpdate();
        let pathSplitted = this.$route.path.split("/");
        this.zanrId = pathSplitted[2];
        this.zanrName = pathSplitted[3];
        this.fetchFilms();
        this.fetchZanrs();
        // console.log(this.zanrName);
        // console.log(this.zanrId);
        // this.fetchZanrByID(this.zanrId);
        // this.fetchFilmByID(this.zanr.filmId);
        // alert(this.zanrId);
        this.getZanrFilm(this.zanrId);
        // console.log(this.film);

    },
    computed: {
        ...mapState([
        'film'
        ])
    },
    methods: {
        ...mapActions([
          'fetchZanrs',
          'fetchFilms'
        ]),
        ...mapMutations([
            'getZanrFilm'
        ]),
        next() {
            if (this.current * 10 == 0) {
                this.current++;
            }
        },
        prev() {
            if (this.current == 1) {
                this.current--;
            }
        },
    },
        components: { FilmComponent }
}

</script>