<template>
  <div class="izmena">
    <h1>Izmena filmova page</h1>
    <h2>{{ filmClicked.name }}</h2>

    <p style="width:500px;word-wrap: break-word;margin-left:auto;margin-right: auto;margin-bottom: 30px;" >Film Description: {{filmClicked.description}}</p>
    <div class="mx-auto text-center" >
      <b-form-textarea
        id="textarea-no-resize"
        v-model="filmClicked.description"
        rows="1"
        no-resize
        style="width:500px;height: 160px; margin-left: auto; margin-right: auto; margin-bottom: 30px;"
      ></b-form-textarea>
    </div>
    <p style="width:500px;word-wrap: break-word;margin-left:auto;margin-right: auto;margin-bottom: 30px;">Film rating: {{filmClicked.rating}}</p>
    <div class="mx-auto text-center" >
      <b-form-textarea
        id="textarea-no-resize"
        v-model="filmClicked.rating"
        rows="1"
        no-resize
        style="width:500px;height: 160px; margin-left: auto; margin-right: auto; margin-bottom: 30px;"
      ></b-form-textarea>
    </div>
    <b-button @click="changeFilm()">Izvrsi promene</b-button>
  </div>
</template>


<script>
import { mapState, mapActions,mapMutations } from 'vuex';

export default{
    name: "Izmena-view",
    // component: {
    //     HelloWorld
    // },
    computed: {
        ...mapState([
        'filmClicked',
        'token'
        ])
    },
    actions: {
    },  
    methods: {
      ...mapActions([
        'putFilm'
      ]),
      ...mapMutations([
        'changeId'
      ]),
      changeFilm: function(){
      const film = {description:this.filmClicked.description,rating: this.filmClicked.rating }
      this.changeId(this.filmClicked.id);
        this.putFilm(film);
      // this.$socket.emit({id:this.filmClicked.id,description:this.filmClicked.description,rating: this.filmClicked.rating, token: this.token})
      }
    },
}


</script>