<template>
    <div class="film d-flex mt-5 mb-5">

        <!-- <img alt="Vue logo" class="mr-5" src="https://www.flixicam.com/img/blog/copy-netflix-download.jpg"> -->
        <img alt="Vue logo" style="width:570px; height:400px;margin-left: 30px; margin-right: 30px;" v-bind:src= "film.trajanje" >
        
        <div class="text-center w-25">
        <h1><u> {{ film.name }}</u></h1>
        <h2>{{ film.description }}</h2>
        <p> {{film.rating }}</p>
        </div>
        <div class="d-flex mx-auto mb-2 w-25">
            <b-button @click="WatchMovie" v-if="ulogovan" class="mx-3 w-25" style="height:40px">Gledaj</b-button>
            <b-button @click="AboutMovie" class="mx-3 w-25" style="height:40px">Opsirnije</b-button>
            <b-button @click="IzmenaMovie" v-if="admin" class="mx-3 w-25" style="height:40px">Izmena</b-button>
        </div>
    </div>
</template>


<script>

import { mapState, mapMutations } from 'vuex';
export default {
  name: 'Film-Comp',
  props: {
    film: Object
  },
      data() {
        return {
            title:"a",
            admins: true
            // ovdee mora da se fetchuje da li je user admin
        };
    },
    methods:{
    ...mapMutations([
        'initMovie'
    ]),
   WatchMovie: function() {
     this.initMovie(this.film);
    if (this.$route.path !== "/watch") {
        this.$router.push("/watch");
    }
   },
    IzmenaMovie: function() {
     this.initMovie(this.film);
    if (this.$route.path !== "/izmena") {
        this.$router.push("/izmena");
    }
   },
    AboutMovie: function() {
     this.initMovie(this.film);
    if (this.$route.path !== "/about") {
        this.$router.push("/about");
    }
   },
 },
    computed: {
        ...mapState([
        'admin',
        'ulogovan'
        ])
    },
}
</script>