import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    items: [],
    slike: [], 
    filmovi: [],
    filmoviZaPrikaz: [],
    film: [],
    zanrovi: [],
    zanr: [],
    useri: [],
    user: [],
    rolovi: [],
    filmClicked: [],
    movieID: -1,
    admin: false,
    initialState: true,
    ulogovan: false,
    token: '',
    username: '',
    uso: false
  },
  getters: {
  },
  mutations: {
    searchFilm(state, filmName) {
      if (filmName === "") {
        state.filmoviZaPrikaz = state.filmovi;
        return;
      }
      state.filmoviZaPrikaz = []
      for(let i = 0; i < state.filmovi.length; i++) {
        if (state.filmovi[i].name.toLowerCase().includes(filmName.toLowerCase())) {
          state.filmoviZaPrikaz.push(state.filmovi[i]);
        }
      }
    },
    addFilms(state, allFilms) {
      state.filmovi = allFilms;
      state.filmoviZaPrikaz = allFilms;
    },
    initMovie(state,film){
      state.filmClicked = film;
    },
    changeId(state,id){
      state.movieID = id;
    },
    addZanrs(state, allZanrs) {
      state.zanrovi = allZanrs;
    },
    addUsers(state, allUsers) {
      state.useri = allUsers;
    },
    addRoles(state, allRoles) {
      state.rolovi = allRoles;
    },
    getZanrByID(state, zanr){
      state.zanr = zanr;
    },
    getFilmByID(state, film){
      state.film = film;
      console.log(state.film);
    },
    getUserByID(state, user){
      state.user = user;
    },
    getZanrFilm(state, zanrId){
      //alert(zanrId);
      for(let i = 0;i<state.zanrovi.length;i++){
        if(state.zanrovi[i].id == zanrId){
            state.zanr = state.zanrovi[i];
        }
      }
      for(let i = 0;i<state.filmovi.length;i++){
        if(state.filmovi[i].id == state.zanr.FilmId){
            state.film = state.filmovi[i];
            return;
        }
      }
      alert("nema filmova iz ovog zanra");
    },
    // getFilmByID1(state,film){
    //   for (let i = 0; i < state.filmovi.length; i++) {
    //     if(state.filmovi[i].id == film.id){
    //       state.film = state.filmovi[i];
    //       console.log((state.filmovi[i]));
    //       break;
    //     }
    //   }
    // },
    setToken1(state, token, username, admin, ulogovan){
        state.token = token;
        localStorage.token = token;
        state.username = username;
        localStorage.username = username;
        state.admin = admin;
        localStorage.admin = admin;
        state.ulogovan = ulogovan;
        localStorage.ulogovan = ulogovan;
    },
    setToken(state, token) {
      // console.log(token[1].username);
        
      state.token = token[0];
      localStorage.token = token[0];
      if(!state.ulogovan){
        state.username = token[1].username;
        localStorage.username = token[1].username;
        state.ulogovan = true;
        localStorage.ulogovan = true;
      
        for(let i=0; i<state.useri.length;i++){
          if(state.useri[i].username == state.username){
              for(let j=0;j<state.rolovi.length;j++){
                if(state.rolovi[j].UserId == state.useri[i].id){
                  state.admin = true;
                }
              }
              state.user = state.useri[i];
          }
        }
      }
    },

    removeToken(state) {
      state.token = '';
      localStorage.token = '';
      state.admin = false;
      localStorage.admin = false;
      state.user = null;
      state.username = '';
      localStorage.username = '';
      state.ulogovan = false;
      localStorage.ulogovan = false;
    },
    addComment(state, obj){
      state.slike = obj.id;
    },
  },
  actions: {
    fetchFilms({ commit }) {
      fetch('http://localhost:8090/admin/films', {
          headers: {
              'Authorization': `Bearer ${localStorage.token}`
          }
      })
            .then( obj => obj.json() )
            .then( res => commit('addFilms', res));
    },
    fetchZanrs({ commit }) {
      fetch('http://localhost:8090/admin/zanrs', {
          headers: {
              'Authorization': `Bearer ${localStorage.token}`
          }
      })
            .then( obj => obj.json() )
            .then( res => commit('addZanrs', res));
    },
    fetchUsers({ commit }) {
      fetch('http://localhost:8090/admin/users', {
          headers: {
              'Authorization': `Bearer ${localStorage.token}`
          }
      })
            .then( obj => obj.json() )
            .then( res => commit('addUsers', res));
    },
    fetchRoles({ commit }) {
      fetch('http://localhost:8090/admin/roles', {
          headers: {
              'Authorization': `Bearer ${localStorage.token}`
          }
      })
            .then( obj => obj.json() )
            .then( res => commit('addRoles', res));
    },
    fetchZanrByID({ commit }, id) {
      fetch(`http://localhost:8090/admin/zanrs/${id}`,{
        headers: {
          'Authorization': `Bearer ${localStorage.token}`
        }
      })
          .then( obj => obj.json() )
          .then( res => commit('getZanrByID', res) );
    },
    fetchUserByID({ commit }, id) {
      fetch(`http://localhost:8090/admin/users/${id}`,{
        headers: {
          'Authorization': `Bearer ${localStorage.token}`
        }
      })
          .then( obj => obj.json() )
          .then( res => commit('getUserByID', res) );
    },
    fetchFilmByID({ commit }, id) {
      fetch(`http://localhost:8090/admin/films/${id}`,{
        headers: {
          'Authorization': `Bearer ${localStorage.token}`
        }
      })
          .then( obj => obj.json() )
          .then( res => commit('getFilmByID', res) );
    },

    login({ commit }, obj) {
      fetch('http://localhost:9000/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
    }).then( res => res.json() )
      .then( tkn => {
        if (tkn.msg) {
          alert(tkn.msg);
        } else {
          commit('setToken', [tkn.token,obj])
        }
      });
    },
    putFilm ( obj) {
      fetch(`http://localhost:8090/admin/films/${ this.state.movieID }`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.token}`
        },
        body: JSON.stringify({description:obj.state.filmClicked.description, rating:obj.state.filmClicked.rating})
      })
          .then(res => res.json())
          .then(data => {
            alert(JSON.stringify({description:obj.state.filmClicked.description, rating:obj.state.filmClicked.rating}));
            if(data.msg){
              //alert("nesto drugo!", data.msg, "error");
              alert(data.msg);
            }
      })
    },


    socket_comment({commit}, msg){
      const comment = JSON.parse(msg);
      commit('addComment', { id:comment.id, description:comment.description,rating:comment.rating,token:comment.token});
    }


  },
  modules: {
  }
})
