<template>
  <div class="films mt-3">
    <HelloWorld msg="Filmovi"/>
    <!-- <FilmComponent msg='1'/>
    <FilmComponent/> -->
    <!-- ovde idu filmovi sinlgle -->

    <!--<div v-for="n in (filmoviZaPrikaz.length < 10 ? filmoviZaPrikaz.length : 10) " :key="n"> 
        <FilmComponent :msg="n"/>
    </div>-->
    <div v-for="n in (filmoviZaPrikaz.length < 10 ? filmoviZaPrikaz.length : 10) " :key="n">
        <FilmComponent v-bind:film="filmoviZaPrikaz[n - 1]" />
    </div>

<!-- filmovi[n].id -->
    <b-button @click="prev()">Previous</b-button>
    <span>...</span>
    <b-button @click="next()">Next</b-button>

    <!-- <div class="overflow-auto" id="ass">
        <b-pagination-nav :link-gen="linkGen" :number-of-pages=this.current use-router></b-pagination-nav>
    </div> -->



  </div>
</template>

<script>

import HelloWorld from '@/components/HelloWorld.vue';
import FilmComponent from '@/components/FilmComponent.vue';
import { mapState } from 'vuex';

    export default{
    name: "Filmovi-view",
    // component: {
    //     HelloWorld
    // },
    data() {
        return {
            current: 10,
            items: [{ message: 'Foo' }, { message: 'Bar' }],
            n: 0

        };
    },
    mounted() {
    },
    computed: {
        ...mapState([
        'filmoviZaPrikaz'
        ])
    },
    methods: {
        next() {
            if (this.current * 10 == 0) {
                this.current++;
            }
        },
        prev() {
            if (this.current == 1) {
                this.current--;
            }
        },
        linkGen(pageNum) {
            return pageNum === 1 ? '?' : `?page=${pageNum}`
      }
    },
    components: { HelloWorld, FilmComponent }
}



</script>

