import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import FilmoviView from '../views/FilmoviView.vue'
import GledanjeView from '../views/GledanjeView.vue'
import ProfileView from '../views/ProfileView.vue'
import Login from '../views/LoginView.vue';
import IzmenaView from '../views/IzmenaView.vue';  
import ZanrView from '../views/ZanrView.vue';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/films',
    name: 'films',
    component: FilmoviView
  },
  {
    path: '/watch',
    name: 'watch',
    component: GledanjeView
  },
  {
    path: '/izmena',
    name: 'izmena',
    component: IzmenaView
  },
  {
    path: '/profile',
    name: 'profile',
    component: ProfileView
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/zanr/:id/:name',
    name: 'Zanr',
    component: ZanrView
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
